<template>
  <va-card :title="$t('tables.labelproducts')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="name_Products_Groups" slot-scope="props">
        {{props.rowData.products_Groups === null ? '-----': props.rowData.products_Groups.name }}
      </template>
      <template slot="name_TaxGroup" slot-scope="props">
        {{props.rowData.taxGroup === null ? '-----': props.rowData.taxGroup.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="dark" @click="showPricingModal(props.rowData.id)" class="ma-0">
          {{ $t('tables.pricing') }}
        </va-button>
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">

          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.nameEn')}} </label>

            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.nameAr')}} </label>

            <va-input v-model="currentobject.nameAr"
              :error="riquierdvalidation(currentobject.nameAr)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.nameAr')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.barcode')}} </label>

            <va-input v-model="currentobject.barcode"
              :placeholder="$t('placeholder.barcode')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.minQtyStock')}} </label>
            <va-input v-model="currentobject.minQtyStock" type="number" min="0"
              :error="numircriquierdvalidation(currentobject.minQtyStock)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.minQtyStock')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.shortCutCode')}} </label>
            <va-input v-model="currentobject.shortCutCode"
              :placeholder="$t('placeholder.shortCutCode')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.uints')}} </label>
            <va-select
              :label="$t('placeholder.uints')"
              v-model="fkUnits.value"
              :error="riquierdvalidation(fkUnits.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkUnits.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.TaxGroup')}} </label>
            <va-select
              :label="$t('placeholder.TaxGroup')"
              v-model="fkTaxGroup.value"
              :error="riquierdvalidation(fkTaxGroup.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkTaxGroup.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.ProductsGroups')}} </label>
            <va-select
              :label="$t('placeholder.ProductsGroups')"
              v-model="fkProductsGroups.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="fkProductsGroups.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.DefultStock')}} </label>
            <va-select
              :label="$t('placeholder.DefultStock')"
              v-model="fkDefultStock.value"
              :error="riquierdvalidation(fkDefultStock.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkDefultStock.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.fkCompanyActivity')"
              v-model="fkCompanyActivity.value"
              :error="riquierdvalidation(fkCompanyActivity.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkCompanyActivity.options"
            />
          </div>

        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}        </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>

    <va-modal v-model="pricingModal" no-outside-dismiss
      :title=" $t('generic.productPrice') "
      :okText=" $t('modal.ok') "
      :cancelText=" $t('modal.cancel') "
      :fullscreen="true"
      @ok="okPricingModal()"
      @cancel="canclePricingModal()">
      <va-card :title="$t('tables.labelproductsPrice')" class="w-100">
        <va-button
          icon="fa fa-plus"
          flat
          slot="actions"
          class="mr-0"
          @click="showCreateModalProductPrice"
        />
        <va-data-table
          :fields="ProductpriceFields"
          :data="dataProductPriceTable"
          :loading="Productpriceloading"
          api-mode
          hoverable
        >
          <template slot="marker" slot-scope="props">
            {{props.rowIndex + 1}}
          </template>
          <template slot="units" slot-scope="props">
            {{props.rowData.units_Products.units === null ? '-----': props.rowData.units_Products.units.name }}
          </template>
          <template slot="units_Parent" slot-scope="props">
            {{props.rowData.units_Products.units_Parent === null ? '-----': props.rowData.units_Products.units_Parent.name }}
          </template>
          <template slot="quantity" slot-scope="props">
            {{props.rowData.units_Products.quantity === null ? '-----': props.rowData.units_Products.quantity }}
          </template>
          <template slot="actions" slot-scope="props">
            <va-button flat small color="gray" @click="showEditModalProductPrice(props.rowData)" class="ma-0">
              {{ $t('tables.edit') }}
            </va-button>

            <va-button flat small color="danger" @click="showDeleteModalProductPrice(props.rowData)" class="ma-0">
              {{ $t('tables.delete') }}
            </va-button>
          </template>
        </va-data-table>

      </va-card>
    </va-modal>

    <va-modal v-model="deleteModalProductPrice" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefunProductPrice()"
      @cancel="cancleDeletefunProductPrice()">
    </va-modal>

    <va-modal v-model="createModalProductPrice" no-outside-dismiss
      size="large"
      :title="$t(modelTitleProductprice)"
      hide-default-actions >
      <form @submit.prevent="editModalProductPrice? okEditfunProductPrice()  :okCreatefunProductPrice()">
        <div class="row flex-center">

          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.uints')"
              v-model="fkUnitsProductPrice.value"
              :error="riquierdvalidation(fkUnitsProductPrice.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkUnitsProductPrice.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-input v-model="currentobjectProductPrice.price" type="number" step="0.5"
              :error="numircriquierdvalidation(currentobjectProductPrice.price)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.price')" required/>
          </div>
          <div class="flex md4 sm12 xs12 px-1">
            <va-input v-model="currentobjectProductPrice.min_Sale_Price" type="number" step="0.5"
              :error="numircriquierdvalidation(currentobjectProductPrice.min_Sale_Price)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.min_Sale_Price')" required/>
          </div>
          <div class="flex md4 sm12 xs12 px-1">
            <va-input v-model="currentobjectProductPrice.max_Sale_Price" type="number" step="0.5"
              :error="numircriquierdvalidation(currentobjectProductPrice.max_Sale_Price)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.max_Sale_Price')" required/>
          </div>
          <div class="flex md4 sm12 xs12 px-1">
            <va-input v-model="currentobjectProductPrice.min_Quantity" type="number" step="1"
              :error="numircriquierdvalidation(currentobjectProductPrice.min_Quantity)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.min_Quantity')" required/>
          </div>
          <va-checkbox
            :label="$t('placeholder.hasParent')"
            v-model="currentobjectProductPrice.hasParent"
          />
        </div>
        <div class="row flex-center" v-if="currentobjectProductPrice.hasParent">

          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.Parent')"
              v-model="fkUnitsProductPrice.valueuintsPriceParent"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fkUnitsProductPrice.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-input v-model="currentobjectProductPrice.units_Products.quantity" type="number" step="1"
              :placeholder="$t('placeholder.quantity')" />
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModalProductPrice? cancleEditfunProductPrice() : cancleCreatefunProductPrice()">
            {{ $t('modal.cancel') }}        </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktextProductprice) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import ProductsSercice from '../../../../services/BasicData/ProductsService'
import ProductsGroupsService from '../../../../services/BasicData/ProductsGroupsService'
import TaxGroupService from '../../../../services/BasicData/TaxGroupService'
import StockService from '../../../../services/BasicData/StockService'
import CompanyActivityService from '../../../../services/BasicData/CompanyActivityService'
import UnitsService from '../../../../services/BasicData/UnitsService'
import UnitsProductsPriceService from '../../../../services/BasicData/UnitsProductsPriceService'
import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      fkTaxGroup: {
        value: '',
        options: [],
      },
      fkProductsGroups: {
        value: '',
        options: [],
      },
      fkCompanyActivity: {
        value: '',
        options: [],
      },
      fkUnits: {
        value: '',
        options: [],
      },
      fkDefultStock: {
        value: '',
        options: [],
      },
      pricingModal: false,
      deleteModal: false,
      editModal: false,
      createModal: false,

      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},

      /* Pricing vars */
      modelTitleProductprice: '',
      modelOktextProductprice: '',
      dataProductPriceTable: [],
      fkUnitsProductPrice: {
        value: '',
        valueuintsPriceParent: '',
        options: [],
      },
      Productpriceloading: true,
      deleteModalProductPrice: false,
      editModalProductPrice: false,
      createModalProductPrice: false,
      opjectBeforeEditProductPrice: {},
      currentobjectProductPrice: {
        units_Products: {},
        hasParent: false,
      },
      currenProductpricingId: '',
      /* end vars */
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
      },
      {
        name: 'barcode',
        title: this.$t('tables.headings.barcode'),
      },
      {
        name: '__slot:name_Products_Groups',
        title: this.$t('tables.headings.name_Products_Groups'),
      },
      {
        name: '__slot:name_TaxGroup',
        title: this.$t('tables.headings.name_TaxGroup'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    ProductpriceFields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      },
      {
        name: '__slot:units',
        title: this.$t('tables.headings.units'),
      },
      {
        name: 'price',
        title: this.$t('tables.headings.price'),
      },
      {
        name: 'min_Sale_Price',
        title: this.$t('tables.headings.min_Sale_Price'),
      },
      {
        name: 'max_Sale_Price',
        title: this.$t('tables.headings.max_Sale_Price'),
      },
      {
        name: '__slot:units_Parent',
        title: this.$t('tables.headings.units_Parent'),
      },
      {
        name: '__slot:quantity',
        title: this.$t('tables.headings.quantity'),
      },
      {
        name: '__slot:actions',
        dataClass: 'text-right',
      },
      ]
    },
  },
  created () {
    this.readItems()
    this.getUnits()
    this.getStocks()
    this.getTaxGroup()
    this.getProductsGroups()
    this.getCompanyActivity()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api get */
    readItems (page = 1) {
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      ProductsSercice.getAllPagnation(params)
        .then(response => {
          console.log(response.data)

          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    ProductpricereadItems (id) {
      this.Productpriceloading = true
      this.currenProductpricingId = id
      UnitsProductsPriceService.getAllPagnation(id)
        .then(response => {
          this.dataProductPriceTable = response.data
          this.Productpriceloading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getTaxGroup () {
      TaxGroupService.getAll()
        .then(response => {
          this.fkTaxGroup.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getStocks () {
      StockService.getAll()
        .then(response => {
          this.fkDefultStock.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getProductsGroups () {
      ProductsGroupsService.getAll()
        .then(response => {
          this.fkProductsGroups.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getCompanyActivity () {
      CompanyActivityService.getAll()
        .then(response => {
          this.fkCompanyActivity.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getUnits () {
      UnitsService.getAll()
        .then(response => {
          this.fkUnits.options = response.data
          this.fkUnitsProductPrice.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    /*  end api get */
    /* cud api */
    create () {
      if (this.fkCompanyActivity.value === '' || this.fkTaxGroup.value === '' || this.fkUnits.value === '' || this.fkDefultStock.value === '' || this.fkDefultStock.value === null || this.fkCompanyActivity.value === null || this.fkTaxGroup.value === null || this.fkUnits.value === null) {
        this.stopBtn = false
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fkDefultStock.value !== '') {
        this.currentobject.fK_FK_DeafultStock = this.fkDefultStock.value.id
      }
      if (this.fkCompanyActivity.value !== '') {
        this.currentobject.fK_Company_Activity = this.fkCompanyActivity.value.id
      }
      if (this.fkProductsGroups.value !== '') {
        this.currentobject.fK_Products_Groups = this.fkProductsGroups.value.id
      }
      if (this.fkTaxGroup.value !== '') {
        this.currentobject.fK_TaxGroup = this.fkTaxGroup.value.id
      }
      if (this.fkUnits.value !== '') {
        this.currentobject.fK_DeafultUnits = this.fkUnits.value.id
      }
      ProductsSercice.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {}
          this.fkTaxGroup.value = ''
          this.fkProductsGroups.value = ''
          this.fkCompanyActivity.value = ''
          this.fkDefultStock.value = ''
          this.fkUnits = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.fkCompanyActivity.value === '' || this.fkTaxGroup.value === '' || this.fkUnits.value === '' || this.fkDefultStock.value === '' || this.fkDefultStock.value === null || this.fkCompanyActivity.value === null || this.fkTaxGroup.value === null || this.fkUnits.value === null) {
        this.stopBtn = false

        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fkDefultStock.value !== '') {
        this.currentobject.fK_FK_DeafultStock = this.fkDefultStock.value.id
      }
      if (this.fkCompanyActivity.value !== '') {
        this.currentobject.fK_Company_Activity = this.fkCompanyActivity.value.id
      }
      if (this.fkProductsGroups.value !== '') {
        this.currentobject.fK_Products_Groups = this.fkProductsGroups.value.id
      }
      if (this.fkTaxGroup.value !== '') {
        this.currentobject.fK_TaxGroup = this.fkTaxGroup.value.id
      }
      if (this.fkUnits.value !== '') {
        this.currentobject.fK_DeafultUnits = this.fkUnits.value.id
      }
      ProductsSercice.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {}
          this.fkTaxGroup.value = ''
          this.fkProductsGroups.value = ''
          this.fkCompanyActivity.value = ''
          this.fkDefultStock.value = ''
          this.fkUnits.value = ''
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    remove (id) {
      ProductsSercice.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.Productpriceloading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fkTaxGroup.value = cuobject.taxGroup
      this.fkProductsGroups.value = cuobject.products_Groups
      this.fkCompanyActivity.value = cuobject.company_Activity
      this.fkDefultStock.value = cuobject.stock
      this.fkUnits.value = cuobject.units
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.editModal = false
      this.createModal = false
      this.loading = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
      this.fkTaxGroup.value = ''
      this.fkProductsGroups.value = ''
      this.fkCompanyActivity.value = ''
      this.fkDefultStock.value = ''

      this.fkUnits.value = ''
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false

      this.loading = false
      this.currentobject = {}
      this.fkTaxGroup.value = ''
      this.fkProductsGroups.value = ''
      this.fkCompanyActivity.value = ''
      this.fkDefultStock.value = ''

      this.fkUnits.value = ''
    },
    /* end create model */

    /* PricingModal model */
    showPricingModal (id) {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.ProductpricereadItems(id)
      this.pricingModal = true
      this.currenProductpricingId = id
    },
    okPricingModal () {
      this.pricingModal = false
      this.loading = false
      this.currenProductpricingId = ''
    },
    canclePricingModal () {
      this.pricingModal = false
      this.loading = false
      this.currenProductpricingId = ''
    },
    /* end create model */

    /* *********************************************************************************************************** */

    /* cud api */
    createProductPrice () {
      if (this.currentobjectProductPrice.price === '' || this.fkUnitsProductPrice.value === '' || this.currentobjectProductPrice.price === null || this.fkUnitsProductPrice.value === null) {
        this.stopBtn = false

        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fkUnitsProductPrice.value !== '') {
        this.currentobjectProductPrice.units_Products.fK_Units = this.fkUnitsProductPrice.value.id
      }
      if (this.currentobjectProductPrice.hasParent === true) {
        if (this.fkUnitsProductPrice.valueuintsPriceParent === null || this.fkUnitsProductPrice.valueuintsPriceParent === '') {
          return this.showToast(this.$t('generic.filldata'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
        this.currentobjectProductPrice.units_Products.fK_Units_Parent = this.fkUnitsProductPrice.valueuintsPriceParent.id
      }
      this.currentobjectProductPrice.units_Products.fK_Products = this.currenProductpricingId
      UnitsProductsPriceService.create(this.currentobjectProductPrice).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.ProductpricereadItems(this.currenProductpricingId)
          this.Productpriceloading = false
          this.currentobjectProductPrice = {
            units_Products: {},
            hasParent: false,
          }
          this.fkUnitsProductPrice.value = ''
          this.fkUnitsProductPrice.valueuintsPriceParent = ''
          this.createModalProductPrice = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    editProductPrice (user) {
      if (this.currentobjectProductPrice.price === '' || this.fkUnitsProductPrice.value === '' || this.currentobjectProductPrice.price === null || this.fkUnitsProductPrice.value === null) {
        this.stopBtn = false

        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fkUnitsProductPrice.value !== '') {
        this.currentobjectProductPrice.units_Products.fK_Units = this.fkUnitsProductPrice.value.id
      }
      if (this.currentobjectProductPrice.hasParent === true) {
        if (this.fkUnitsProductPrice.valueuintsPriceParent === null || this.fkUnitsProductPrice.valueuintsPriceParent === '') {
          return this.showToast(this.$t('generic.filldata'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
        this.currentobjectProductPrice.units_Products.fK_Units_Parent = this.fkUnitsProductPrice.valueuintsPriceParent.id
      }
      this.currentobjectProductPrice.units_Products.fK_Products = this.currenProductpricingId
      UnitsProductsPriceService.update(this.currentobjectProductPrice.id, this.currentobjectProductPrice).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.ProductpricereadItems(this.currenProductpricingId)
          this.Productpriceloading = false
          this.currentobjectProductPrice = {
            units_Products: {},
            hasParent: false,
          }
          this.fkUnitsProductPrice.value = ''
          this.fkUnitsProductPrice.valueuintsPriceParent = ''
          this.editModalProductPrice = false
          this.createModalProductPrice = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    removeProductPrice (id) {
      UnitsProductsPriceService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataProductPriceTable.findIndex(u => u.id === id)
          this.dataProductPriceTable.splice(idx, 1)
          this.Productpriceloading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.Productpriceloading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModalProductPrice (cuobject) {
      this.deleteModalProductPrice = true
      this.currentobjectProductPrice = cuobject
      this.Productpriceloading = true
    },
    okDeletefunProductPrice () {
      this.removeProductPrice(this.currentobjectProductPrice.id)
    },
    cancleDeletefunProductPrice () {
      this.Productpriceloading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModalProductPrice (cuobject) {
      if (cuobject.units_Products.units_Parent !== null) {
        cuobject.hasParent = true
        this.fkUnitsProductPrice.valueuintsPriceParent = cuobject.units_Products.units_Parent
      }
      this.editModalProductPrice = true
      this.modelTitleProductprice = 'generic.editItemTitle'
      this.modelOktextProductprice = 'modal.edit'
      this.createModalProductPrice = true

      this.fkUnitsProductPrice.value = cuobject.units_Products.units
      this.opjectBeforeEditProductPrice = Object.assign({}, cuobject)
      this.currentobjectProductPrice = cuobject
      this.Productpriceloading = true
    },
    okEditfunProductPrice () {
      this.stopBtn = true
      this.editProductPrice(this.currentobjectProductPrice)
    },
    cancleEditfunProductPrice () {
      this.createModalProductPrice = false
      this.editModalProductPrice = false
      this.Productpriceloading = false
      Object.assign(this.currentobjectProductPrice, this.opjectBeforeEditProductPrice)
      this.currentobjectProductPrice = {
        units_Products: {},
        hasParent: false,
      }
      this.fkUnitsProductPrice.valueuintsPriceParent = ''
      this.fkUnitsProductPrice.value = ''
    },
    /* end edit model */

    /* create model */
    showCreateModalProductPrice () {
      this.modelTitleProductprice = 'generic.creatItemTitle'
      this.modelOktextProductprice = 'modal.create'
      this.createModalProductPrice = true
      this.Productpriceloading = true
    },
    okCreatefunProductPrice () {
      this.stopBtn = true
      this.createProductPrice()
    },
    cancleCreatefunProductPrice () {
      this.createModalProductPrice = false

      this.Productpriceloading = false
      this.currentobjectProductPrice = {
        units_Products: {},
        hasParent: false,
      }

      this.fkUnitsProductPrice.valueuintsPriceParent = ''
      this.fkUnitsProductPrice.value = ''
    },
    /* end create model */
  },
}

</script>

