import http from '../../http-common'

class UnitsProductsPriceSercice {
  getAll () {
    return http.get('Units_Products_Price')
  }

  getAllPagnation (id) {
    return http.get(`Units_Products_Price?Id_Product=${id}`)
  }

  get (id) {
    return http.get(`Units_Products_Price?Id=${id}`)
  }

  create (data) {
    return http.post('Units_Products_Price', data)
  }

  update (id, data) {
    return http.put(`Units_Products_Price?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Units_Products_Price?Id=${id}`)
  }
}

export default new UnitsProductsPriceSercice()
