import http from '../../http-common'

class CompanyActivityService {
  getAll () {
    return http.get('CompanyActivity')
  }

  get (id) {
    return http.get(`CompanyActivity?Id=${id}`)
  }

  create (data) {
    return http.post('CompanyActivity', data)
  }

  update (id, data) {
    return http.put(`CompanyActivity?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`CompanyActivity?Id=${id}`)
  }
}

export default new CompanyActivityService()
